.swiper-button-prev,
.swiper-button-next {
  @include easeOut;
  z-index: 5;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  margin-top: initial;
  border-radius: 50%;
  background-image: linear-gradient(to right, rgb(101, 65, 163) 0%, rgb(70, 100, 188) 100%); // &::before {
  background-size: contain;
  border: 1px solid rgb(70, 100, 188);
  transform: rotate(180deg);

  &:hover {
    background: rgb(255, 255, 255);

    svg {
      fill: rgb(70, 100, 188);
    }
  }

  svg {
    @include easeOut;
    fill: white;
    width: 14px;
    height: 20px;
  }
}

.swiper-button-next {
  svg {
    transform: rotate(180deg);
  }
}

.swiper-button-next.swiper-button-disabled,
.swiper-button-prev.swiper-button-disabled {
  display: none;
}

.swiper-container-horizontal>.swiper-pagination-bullets {
  bottom: 50px;
  height: 17px;
}

.swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet {
  position: relative;
  margin: 0 5px;

  &::before {
    @include absolute-center;
    @include easeOut;
    content: "";
    width: 5px;
    height: 5px;
    background-color: transparent;
  }

  &:hover {
    &::before {
      background-color: rgb(70, 100, 188);
    }
  }
}

.swiper-pagination {
  z-index: 4;
}

.swiper-pagination-bullet {
  width: 15px;
  height: 15px;
  border: 1px solid rgb(70, 100, 188);
  background: transparent;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  background-color: rgb(70, 100, 188);
}

@include media-min($tablet-width) {
  .swiper-container-horizontal>.swiper-pagination-bullets {
    bottom: 46px;
  }
}
